




















































import Swal from 'sweetalert2';
import axios from 'axios';
import { Component, Vue, Ref } from 'vue-property-decorator';
import { State, Action } from 'vuex-class';
import GoogleLogin from 'vue-google-login';
import { RootState } from '@/store/types';
import { IInputInfo, ILoginPayload, IFacebookPayload } from '@/types/interface';
import { getSnapCode } from '@/utility';
import inputFormPanel from '../components/InputFormPanel.vue';
import * as commonTypes from '../types/common';

@Component({
  components: {
    inputFormPanel,
    GoogleLogin,
  },
})

export default class Login extends Vue {
  @State('registerFlag') registerFlag!: RootState
  @State('token') token!: string
  @Action('changeRegisterModeAct') changeRegisterModeAct!: (flag: boolean) => void
  @Action('refreshToken') refreshToken!: () => Promise<void>
  @Action('addMember') addMember!: (params: IInputInfo) => Promise<ILoginPayload>;
  @Action('loginAndLoadBaseUser') loginAndLoadBaseUser!: (params: IInputInfo) => Promise<ILoginPayload>;
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  private types = commonTypes;
  errorTxt = '';
  disableLogin = false;
  disableRegister = false;
  autoLoginFlag = false;

  params = {
    // eslint-disable-next-line
    client_id: '147648262962-3v287vcbitvkd1lk7sv9ln24cdoqkngl.apps.googleusercontent.com',
  };

  renderParams = {
    height: 46,
    longtitle: true,
  };

  get inputList() {
    return this.registerFlag ? [this.types.INPUT_PHONE, this.types.INPUT_PASSWORD, this.types.INPUT_CAPTCHA]
      : [this.types.INPUT_PHONE, this.types.INPUT_PASSWORD];
  }

  private link() {
    this.$router.push('/login/assignment');
  }

  forget() {
    this.$router.push('/login/forget_password');
  }

  private registerProcess() {
    this.errorTxt = '';
    this.inputForm.resetPassword();
    this.changeRegisterModeAct(true);
  }

  private loginProcess() {
    this.errorTxt = '';
    this.inputForm.resetPassword();
    this.changeRegisterModeAct(false);
  }

  created() {
    if (this.token === '') {
      this.refreshToken()
        .then(() => {
          const localTemp = localStorage.getItem('ckapp');
          if (localTemp) {
            const localParse = JSON.parse(atob(localTemp));
            this.loginAndLoadBaseUser(localParse)
              .then((data: ILoginPayload) => {
                if (data.Code === '0000') this.$router.push('/main');
                else this.autoLoginFlag = true;
              })
              .catch((err) => console.error(err));
          } else this.autoLoginFlag = true;
          // const filterTemp = document.cookie.split('; ').filter((d: string) => d.indexOf('ckapp') >= 0);
          // if (filterTemp.length === 1) {
          //   const cookieSplitTemp = filterTemp[0].split('ckapp=')[1];
          //   if (cookieSplitTemp !== '') {
          //     const cookieTemp = JSON.parse(atob(cookieSplitTemp));
          //     this.loginAndLoadBaseUser(cookieTemp)
          //       .then((data: ILoginPayload) => {
          //         if (data.Code === '0000') this.$router.push('/main');
          //         else this.autoLoginFlag = true;
          //       })
          //       .catch((err) => console.error(err));
          //   } else this.autoLoginFlag = true;
          // } else this.autoLoginFlag = true;
        });
    } else this.autoLoginFlag = true;
  }

  sendCode(phone: string) {
    getSnapCode({
      Source: 'CF05',
      Mobile: phone,
    });
  }

  registerHandler() {
    if (this.disableRegister) return;
    const info: IInputInfo | undefined = this.inputForm.getInput();
    if (info) {
      this.disableRegister = true;
      info.Status = '0';
      this.addMember(info)
        .then((data: ILoginPayload) => {
          this.disableRegister = false;
          if (data.Code === '9999') this.errorTxt = data.Message || '';
          if (data.Code === '0000') {
            this.loginAndLoadBaseUser(info)
            .then((loadData: ILoginPayload) => {
              if (loadData.Code === '0000') this.$router.push('/member/association');
            });
          }
        });
    }
  }

  loginHandler() {
    if (this.disableLogin) return;
    const info: IInputInfo | undefined = this.inputForm.getInput();
    if (info) {
      this.disableLogin = true;
      this.loginAndLoadBaseUser(info)
        .then((data: ILoginPayload) => {
          if (data.Code === '8888') {
            this.disableLogin = false;
            Swal.fire({
              toast: true,
              width: 300,
              position: 'top',
              showConfirmButton: false,
              showCloseButton: true,
              background: '#FBF2F2',
              buttonsStyling: false,
              customClass: {
                closeButton: 'toast-close-btn',
                popup: 'toast-popup',
                title: 'toast-title',
              },
              title: '密碼累積錯誤5次，已鎖定帳號15分鐘',
            });
          } else if (data.Code === '9999') {
            this.disableLogin = false;
            this.errorTxt = '帳號密碼錯誤';
          } else if (data.Code === '0000') {
            this.$router.push('/main');
          }
        })
        .catch(() => {
          this.disableLogin = false;
          console.log('error');
          this.errorTxt = '帳號密碼錯誤';
        });
    }
  }

  loginFacebook() {
    // eslint-disable-next-line
    const popup = window.open(`https://www.facebook.com/dialog/oauth?client_id=295227721702470&display=popup&scope=email&response_type=token,granted_scopes&auth_type=rerequest&redirect_uri=https://moea.career-up.com.tw/FbLogin.html`, 'Facebook Login', 'width=500,height=500');
    const getToken: Promise<string> = new Promise<string>((resolve, reject) => {
      let finished = false;
      const listener = (e: MessageEvent) => {
        finished = true;
        const url = new URL(e.data);
        const hash = url.hash.substring(1);
        const splitted = hash.split('&');
        const dct: { [key: string]: string } = {};
        splitted.forEach((s: string) => {
          const spltd = s.split('=');
          // eslint-disable-next-line prefer-destructuring
          dct[spltd[0]] = spltd[1];
        });

        if (dct.granted_scopes.indexOf('email') < 0) {
          // eslint-disable-next-line prefer-promise-reject-errors
          reject('Email required');
          return;
        }
        resolve(dct.access_token);
      };

      window.addEventListener('message', listener);

      const intervalChecker = setInterval(() => {
        if (popup && popup.closed) {
          clearInterval(intervalChecker);
          window.removeEventListener('message', listener);
          if (!finished) {
            // eslint-disable-next-line prefer-promise-reject-errors
            reject('Login canceled');
          }
        }
      }, 10);
    });

    function getProfile(token: string): Promise<IFacebookPayload> {
      return new Promise<IFacebookPayload>((resolve, reject) => {
        axios
          .get(`https://graph.facebook.com/v2.5/me?access_token=${token}&fields=name,id,email`)
          .then((res) => {
            if (res.status === 200) resolve(res.data);
            reject();
          })
          .catch(() => reject());
      });
    }

    let params: IInputInfo | {} = {};

    getToken
      .then((token) => getProfile(token))
      .then((data: IFacebookPayload) => {
        params = {
          Source: 'CF01',
          Name: data.name,
          // eslint-disable-next-line
          Approve_Key: data.id,
          Email: data.email,
        };
        // if (this.registerFlag) return this.addMember(params);
        return this.loginAndLoadBaseUser(params);
      }).then((data: ILoginPayload) => {
        if (data.Code === '0000') this.$router.push('/main');
        if (data.Code === '9999') {
          this.addMember(params)
            .then(() => {
              this.$router.push('/login/set_mobile');
            });
        }
      }).catch((err) => {
        Swal.fire({
          width: 300,
          showCloseButton: false,
          background: '#FFFFFF',
          title: '登入錯誤',
          text: `${err}`,
          customClass: {
            title: 'modal-title',
            content: 'modal-content',
            confirmButton: 'modal-confirm-button',
          },
          confirmButtonText: '確定',
        });
      });
  }

  onSuccess(googleUser: any) {
    const profile = googleUser.getBasicProfile();

    const params: IInputInfo = {
      Source: 'CF02',
      Name: profile.getName(),
      // eslint-disable-next-line
      Approve_Key: profile.getId(),
      Email: profile.getEmail(),
    };

    this.loginAndLoadBaseUser(params)
      .then((data: ILoginPayload) => {
        if (data.Code === '0000') this.$router.push('/main');
        if (data.Code === '9999') {
          this.addMember(params)
            .then(() => {
              this.$router.push('/login/set_mobile');
            });
        }
      })
      .catch((err) => {
        Swal.fire({
          width: 300,
          showCloseButton: false,
          background: '#FFFFFF',
          title: '登入錯誤',
          text: `${err}`,
          customClass: {
            title: 'modal-title',
            content: 'modal-content',
            confirmButton: 'modal-confirm-button',
          },
          confirmButtonText: '確定',
        });
      });
  }
}
